/* Scroll Indicators Styles */

/* Left Indicator */
.indicator-left {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: var(--z-indicators);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--gutter);
  pointer-events: none;
}

.indicator-left > * {
  pointer-events: auto;
}

.indicator-left__about {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  text-transform: uppercase;
  letter-spacing: var(--letter-spacing-wider);
  color: var(--color-text);
  opacity: 0.6;
  transition: opacity var(--duration-fast) var(--ease-out);
  cursor: pointer;
}

.indicator-left__about:hover,
.indicator-left__about--active {
  opacity: 1;
}

.indicator-left__project {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.indicator-left__number {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  letter-spacing: var(--letter-spacing-wide);
  color: var(--color-text);
  opacity: 0.6;
}

.indicator-left__title {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  text-transform: uppercase;
  letter-spacing: var(--letter-spacing-wider);
  color: var(--color-text);
}

.indicator-left__client {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  text-transform: uppercase;
  letter-spacing: var(--letter-spacing-wider);
  color: var(--color-text);
  opacity: 0.6;
}

.indicator-left__archive {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  text-transform: uppercase;
  letter-spacing: var(--letter-spacing-wider);
  color: var(--color-text);
  opacity: 0.6;
  transition: opacity var(--duration-fast) var(--ease-out);
  cursor: pointer;
}

.indicator-left__archive:hover,
.indicator-left__archive--active {
  opacity: 1;
}

/* Right Indicator */
.indicator-right {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: var(--z-indicators);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: var(--gutter);
  pointer-events: none;
}

.indicator-right > * {
  pointer-events: auto;
}

.indicator-right__numbers {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: var(--space-8);
  padding-top: var(--space-8);
}

.indicator-right__number {
  position: relative;
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-normal);
  letter-spacing: var(--letter-spacing-wide);
  color: var(--color-text);
  opacity: 0.4;
  transition: opacity var(--duration-fast) var(--ease-out);
  cursor: pointer;
}

.indicator-right__number:hover {
  opacity: 0.8;
}

.indicator-right__number--active {
  opacity: 1;
}

/* Moving square indicator */
.indicator-right__marker {
  position: absolute;
  right: 0;
  width: 8px;
  height: 8px;
  background-color: var(--color-white);
  transition: top var(--duration-normal) var(--ease-out);
}

/* Header (Logo) */
.header {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: var(--z-header);
  padding: var(--gutter);
  pointer-events: none;
}

.header__logo {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-light);
  letter-spacing: var(--letter-spacing-wide);
  color: var(--color-text);
  text-align: center;
  pointer-events: auto;
  cursor: pointer;
}

.header__logo a {
  color: inherit;
  text-decoration: none;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .indicator-left {
    padding: var(--space-3);
  }

  .indicator-left__about,
  .indicator-left__archive {
    font-size: 0.5rem;
  }

  .indicator-left__project {
    gap: var(--space-1);
  }

  .indicator-left__number,
  .indicator-left__title,
  .indicator-left__client {
    font-size: 0.5rem;
  }

  .indicator-right {
    padding: var(--space-3);
  }

  .indicator-right__numbers {
    gap: var(--space-5);
    padding-top: var(--space-5);
  }

  .indicator-right__number {
    font-size: 0.5rem;
  }

  .indicator-right__marker {
    width: 6px;
    height: 6px;
  }

  .header {
    padding: var(--space-3);
  }

  .header__logo {
    font-size: var(--font-size-base);
  }
}

@media (max-width: 480px) {
  .indicator-left__project {
    display: none;
  }

  .indicator-right__numbers {
    gap: var(--space-4);
  }
}

